.body_slides {
  list-style: none;
  margin: 0;
  padding: 0;
  z-index: -5;
  background: #000;
}

.body_slides,
.body_slides:after {
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
}

.body_slides li {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-size: cover;
  background-repeat: none;
  opacity: 0;
  -webkit-animation: anim_slides 12s linear infinite 0s;
  -moz-animation: anim_slides 12s linear infinite 0s;
  -o-animation: anim_slides 12s linear infinite 0s;
  -ms-animation: anim_slides 12s linear infinite 0s;
  animation: anim_slides 12s linear infinite 0s;
}

.body_slides li:nth-child(1) {
  background-image: url(../img/asphalt-empty-field-163444.png);
  background-size: cover;
  width: 100%;
  background-position: center;
}

.body_slides li:nth-child(2) {
  -webkit-animation-delay: 4.0s;
  -moz-animation-delay: 4.0s;
  background-image: url(../img/img23.png);
  background-size: cover;
  width: 100%;
  background-position: center;
}

.body_slides li:nth-child(3) {
  -webkit-animation-delay: 8.0s;
  -moz-animation-delay: 8.0s;
  background-image: url(../img/action-athletes-audience-1884576.png);
  background-size: cover;
  width: 100%;
  background-position: center;
}

@-webkit-keyframes anim_slides {
  0% {
    opacity: 0;
  }

  6% {
    opacity: 1;
  }

  24% {
    opacity: 1;
  }

  30% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}

@-moz-keyframes anim_slides {
  0% {
    opacity: 0;
  }

  6% {
    opacity: 1;
  }

  24% {
    opacity: 1;
  }

  30% {
    opacity: 0;
  }

  100% {
    opacity: 0;
  }
}

